.telegram-instructions {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 20px;
  }
  
  .instruction-section {
    margin: 20px 0;
  }
  
  h2, h3 {
    margin: 10px 0;
  }
  
  ol {
    text-align: left;
    display: inline-block;
  }
  
  a {
    color: #1a73e8;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  