/* Styling for the footer container */
.hodlinfo-footer {
    display: flex; /* Flexbox for centering */
    justify-content: center; /* Horizontally center */
    align-items: center;
    border: 1px solid rgb(25, 29, 40);
    /* background-color: rgb(25, 29, 40); */
    background-color: #191d28;
    position: fixed;
    left: 0px;
    width: 100vw;
    height: 47px;
    bottom: 0px;
    z-index: 8;
  }
  
  /* Styling for the button */
  .add-button {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    color: #17a2b8; /* Bootstrap info color */
    border: 2px solid #17a2b8;
    background-color: transparent;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .add-button:hover {
    background-color: #17a2b8;
    color: white;
  }
  