.price-display-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    /* background-color: #1d1d1d; */
  background-color: #191d28;

    color: #eaeaea;
    padding: 20px;
    border-radius: 10px;
  }
  
  .price-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  
  .price-center {
    text-align: center;
  }
  
  .price-change h2 {
    font-size: 24px; /* Adjust this to match the 0.1% size */
    margin: 0;
    color: #00ff00; /* Bright green */
  }
  
  .price-change p {
    font-size: 14px;
    color: #c0c0c0;
  }
  
